<template>
  <div class="col-12 col-md-6 col-lg-6 col-xl-4">
    <div class="card-item">
      <a v-on:click="cardClick">
        <img class="card-image" :src="image">
        <div class="card-title">{{ title }}</div>
        <div class="card-content">{{ content }}</div>
        <div class="card-link">{{ link }}</div>
      </a>
    </div>

    <div v-show="isMessageVisible" class="message">
      Clicked: Message in console
    </div>

  </div>
</template>

<script>
export default {
  name: 'BlockCard',

  props: [
    'image', 
    'title',
    'content',
    'link',
  ],

  data() {
    return {
      isMessageVisible: false,
    }
  },

  methods: {
    cardClick: function() {
      // eslint-disable-next-line
      console.log('You have clicked on: ' + this.title)
      this.isMessageVisible = true;
      setTimeout(() => this.isMessageVisible = false, 1000);
    }
  },
  
}
</script>

<!-- Added "scoped" attribute to limit CSS to this component only -->
<style scoped>

.card-item {
  padding: 15px;
  margin-bottom: 15px;
  transition: all 200ms ease-in;
}

.card-item:hover {
  transform: scale(1.05) translateY(5px);
  /*box-shadow: 7px 9px 18px -2px rgba(61,61,61,1);*/
  cursor: pointer;
}

.card-image {
  max-width: 100%;
  width: 95%;
  height: auto;
  margin-bottom: 25px;
  border-bottom: 4px #CF1430 solid;
}

.card-title {
  color: #FFFFFF;
  font-family: "Open Sans";
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.5px;
  line-height: 30px;
}

.card-content {
  color: #C2C2C2;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
  margin-bottom: 25px;
}

.card-link {
  color: #FFFFFF;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 600;
  line-height: 30px;
  width: 94px;
  border-bottom: 1px solid #CF1430;
  margin-bottom: 50px;
}

.message {
  position: absolute;
  top: 5px;
  left: 5px;
  background-color: #000000;
  color: #C2C2C2;
  font-size: 16px;
  padding: 7px 14px;
  border: none;
  border-radius: 10px;
}

</style>
