<template>
  <transition name="modal-fade">
    <div class="modal-backdrop">
      <div class="modal-item" role="modal" aria-labelledby="modalTitle" aria-describedby="modalDescription">
        
        <section class="modal-body" id="modalDescription">
          <slot name="body">
            THIS IS THE DEFAULT BODY
          </slot>
        </section>

        <button type="button" class="btn-close" @click="close" aria-label="Close modal">
          X
        </button>

      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'Block1Modal',

  methods: {
    close() {
      this.$emit('close');
    },
  },

};
</script>

<style scoped>

.modal-fade-enter,
.modal-fade-leave-active {
  opacity: 0;
}

.modal-fade-enter-active,
.modal-fade-leave-active {
  transition: opacity .5s ease
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-item {
  background: #000000;
  box-shadow: 2px 2px 20px 1px;
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 100%;
  max-height: 100%;
}

.modal-item:hover {
  cursor: pointer;
}

.modal-body {
  position: relative;
  padding: 20px 10px;
}

/* Style the button and place it in the middle of the container/image */
.btn-close {
  position: absolute;
  top: 15px;
  right: 10px;
  background-color: #000000;
  color: white;
  font-size: 16px;
  padding: 7px 14px;
